import { Box, Calendar, DropButton, DropButtonProps, Text, TextInput, MaskedInput } from "grommet";
import { Calendar as CalendarIcon, FormDown } from "grommet-icons";
import React, { Component } from "react";
import moment from "moment";
import { Masks } from "../../../constants";

type NewProps = Omit<DropButtonProps, "dropContent">;

export interface CalendarDropDownProps extends NewProps {
	date: Date;
	onChange: ((date: changedEvent) => void);
	calendarSize?: "small" | "medium" | "large";
};

type changedEvent = {
	target: CalendarDropDown,
	value: Date
}

export default class CalendarDropDown extends Component<CalendarDropDownProps> {
	public state = {
		date: this.props.date,
		szDate: moment(this.props.date).local().format("DD/MM/YYYY"),
	};
	constructor(p: CalendarDropDownProps) {
		super(p);
	}

	public render() {
		return (
			<Box width="180px">
				<DropButton
					open={this.props.open}
					onClose={() => this.setState({ open: false })}
					onOpen={() => this.setState({ open: true })}
					dropContent={this.renderCalendar()}
					{...this.props}
				>
					<Box direction="row" gap="2px" align="center" justify="start" pad={{vertical: "small"}}>
						<Box width="32px">
							<i className="zwicon-calendar-month" style={{
								color: "#9060EB"
							}}/>
						</Box>

						<Box >
							<TextInput width="auto" 
								value={new Date(this.props.date).toDateString()}
							/>
						</Box>
					</Box>
				</DropButton>
			</Box>
		);
	}

	//private onDateInputed

	private renderCalendar = () => (
		<Box pad="small">
			<Box direction="row" gap="2px" align="center" pad={{vertical: "small"}}>
				<Box width="32px">
					<i className="zwicon-calendar-month" style={{
						color: "#9060EB"
					}}/>
				</Box>

				<MaskedInput value={this.state.szDate} mask={Masks.date}
					onChange={(event: any) => {
						const value = event.target.value;
						this.setState({szDate: value});
					}}
					onBlur={(event: any) => {
						try {
							const m = moment(event.target.value, "DD/MM/YYYY");
							const date = m.toDate();

							this.setState({szDate: m.format("l"), date});

							if (this.props.onChange) {
								this.props.onChange({ target: this, value: date });
							}
						} catch {}
					}}
				/>
			</Box>
			
			<Box margin={{top: "small"}}>
				<Calendar 
					color="secondary"
					size={this.props.calendarSize ? this.props.calendarSize : "medium"}
					date={this.props.date.toISOString()}
					onSelect={
						(d: any) => {
							const m = moment(new Date(d));
							const date = m.toDate();

							this.setState({szDate: m.format("l"), date});

							if (this.props.onChange) {
								this.props.onChange({ target: this, value: date });
							}
						}
					}
				/>
			</Box>
		</Box>
	)
}
